
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
name: 'Detail',
data () {
return {
list: [{
key: 'cx',
value: '储蓄'
}]
}
},
computed: {
isSyn () {
return this.$store.state.device.isSyn
},
treasure () {
return this.$store.getters.get_treasure
},
children () {
return this.$store.getters.get_children
},
swidth () {
let list = this.children
let len = list.length
let iscollection = this.treasure.iscollection
iscollection && (len += 0.8)
return 80 * len
},
sheight () {
let height = this.$store.state.device.height
let list = this.children
let len = list.length
height = height - 330
height = len ? height - 50 : height
return `${height}px`
},
billes () {
return this.$store.getters.get_billes
}
},
methods: {

swipe () {
this.$emit('swipe', 1)
},

syn () {

if (window.applicationCache.status === window.applicationCache.UPDATEREADY) {
window.applicationCache.update()
}
window.location.reload()

},

setCurrent (code) {
this.$store.commit('tr_set_current', code)
},

up () {
let code = this.treasure.code.slice(0, -3)
this.$store.commit('tr_set_current', code)
},

viewInfo () {
let code = this.treasure.code
if (code !== '00') {
this.$router.push({
name: 'Info',
params: {
isCurrent: 1
}
})
}
},

view (item) {
console.log(this.prompt)
this.$router.push({
path: '/bill',
query: {
...item
}
})
},

add () {
let list = this.$store.getters.get_wealth
let len = list.length
if (len) {
this.$router.push({
path: '/bill'
})
} else {
this.$prompt('当前集合没有财富，请创建')
}
}
},
components: {
XHeader, Scroller
}
}



