
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import Detail from './Detail'
import Analyze from './Analyze'
export default {
name: 'Home',
data () {
return {
tabIndex: 0
}
},
computed: {
height () {
return `${this.$store.state.device.height}px`
}
},
methods: {
swipe (index) {
this.tabIndex = index
}
},
components: {
Swiper, SwiperItem, Detail, Analyze
}
}



