
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import _ from 'lodash'
import { Check } from '@/libs/check'
import { compareObj } from '@/libs/util'
import mixin from '@/libs/vue/mixin'

export default {
name: 'Info',
mixins: [mixin],
data () {
return {
treasure: {
name: '',
icon: 'defaultw.png',
parent: '',
iscollection: false,
short: '',
note: ''
}
}
},
computed: {
icons () {
return this.$store.getters.get_icons
},
collection () {
return this.$store.getters.get_collection
}
},
activated () {
let isCurrent = this.$route.params.isCurrent
if (+isCurrent) {
_.assign(this.treasure, this.$store.getters.get_treasure)
let code = this.treasure.code.slice(0, -3)
this.treasure.parent = code
} else {
this.treasure.parent = this.$store.state.treasure.current
}
},
methods: {

setIcon (icon) {
this.treasure.icon = icon
},

save () {
let self = this
let treasure = _.assign({}, this.treasure)

let flag = new Check(treasure, {
name: {
auth: ['NotNull'],
mess: ['请输入财富名称']
}
}).then((message) => {
this.$prompt(message)
})
if (!flag) {
return
}

if (treasure.id) {
let old = _.assign({}, this.$store.getters.get_treasure)
let code = old.code.slice(0, -3)
old.parent = code
let changes = compareObj(treasure, old)
treasure.changes = changes
}
this.$store.dispatch('save_treasure', {
treasure,
handle () {
console.log('保存成功')
self.treasure = {
name: '',
icon: 'defaultw.png',
parent: '',
iscollection: false,
short: '',
note: ''
}
self.goBack()
}
})
}
},
components: {
XHeader, Group, XInput, Selector, Swiper, SwiperItem, Scroller, XSwitch, XTextarea
}
}



