
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import _ from 'lodash'
import { diff } from '@/libs/util'
export default {
name: 'app',
computed: {
...mapGetters([
'prompt',
'showLoading',
'toastOPT'
]),
tList () {
return this.$store.getters.get_tList
},
bList () {
return this.$store.getters.get_bList
}
},
watch: {
tList: {
handler (val, old) {
let todoList = diff(val, old)
console.log('tList')
console.log(todoList)
todoList = _.map(todoList, item => {
return {
...item,
flag: 'treasure'
}
})
this.$store.dispatch('dv_syn_dev', {
todoList
})
},
deep: true
},
bList (val, old) {
let todoList = diff(val, old)
console.log('bList')
console.log(todoList)
todoList = _.map(todoList, item => {
return {
...item,
flag: 'bill'
}
})
this.$store.dispatch('dv_syn_dev', {
todoList
})
}
},
mounted () {
this.$store.dispatch('dv_init_loc')
},
methods: {
closePromp () {
this.closePromt()
let callback = this.prompt.callback
!!callback && callback()
},
...mapMutations({
closePromt: 'CLOSE_PROMPT'
})
},
components: {
Loading, XDialog, Toast
}
}




