
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import moment from 'moment'
import _ from 'lodash'
import { Check } from '@/libs/check'
import mixin from '@/libs/vue/mixin'

const Today = moment().format('YYYY-MM-DD')

export default {
name: 'Bill',
mixins: [mixin],
data () {
return {
tabIndex: 0,
selected: 0,
amount: '',
money: '',
date: Today,
recorded: false,
note: '',
id: ''
}
},
computed: {
wealth () {
return this.$store.getters.get_wealth
},
treasure () {
let index = this.selected
return this.wealth[index] || {}
}
},
watch: {
tabIndex (val, old) {
this.calc(val)
}
},
activated () {

console.log(this.treasure)
/* if (!this.treasure.icon) {
this.$router.push({
name: 'Info',
params: {
isCurrent: 0
}
})
return
} */
let query = this.$route.query
let {id, code, money, date, recorded, note} = query
if (code) {
this.selected = _.findIndex(this.wealth, _.matchesProperty('code', code))
this.money = money
this.date = date
this.recorded = recorded
this.note = note
this.id = id
this.tabIndex = 1
}
},
deactivated () {

this.tabIndex = 0
this.selected = 0
this.amount = ''
this.money = ''
this.date = Today
this.recorded = false
this.note = ''
this.id = ''
},
methods: {

calc (val) {
let last = this.treasure.amount
let amount = this.amount
let money = this.money
let lastMoney = +(this.$route.query.money || 0)
if (val && amount !== '') {
this.money = (amount - last + lastMoney).toFixed(2)
} else if (!val && money !== '') {
let diff = (money - lastMoney).toFixed(2)
this.amount = (+last + +diff).toFixed(2)
}
},

save () {
this.calc(1)
let self = this
let trId = this.treasure.id
let {mgdbId, code} = this.treasure
let {id, money, date, recorded, note} = this
let lastMoney = +(this.$route.query.money || 0)
let lastCode = this.$route.query.code
let diff = (money - lastMoney).toFixed(2)
let bill = {
id,
trId: trId,
trMgdbId: mgdbId,
money,
date,
recorded,
note,
code,
diff
}

if (lastCode && lastCode !== code) {
this.$store.dispatch('calc_amount', {
code: lastCode,
diff: -lastMoney
})
bill.diff = money
}

let flag = new Check(bill, {
money: {
auth: ['NotNull'],
mess: ['请输入账单金额']
}
}).then((message) => {
this.$prompt(message)
})
if (flag) {
this.$store.dispatch('save_bill', {
bill,
handle () {
console.log('保存成功')
self.goBack()
}
})
}
},

remove () {
let {id, code, money} = this.$route.query
let self = this
this.$store.dispatch('remove_bill', {
bill: {
id,
code,
money
},
handle () {
console.log('删除成功')
self.goBack()
}
})
},

viewInfo () {
let code = this.treasure.code
this.$store.commit('tr_set_current', code)
this.$router.push({
name: 'Info',
params: {
isCurrent: 1
}
})
}
},
components: {
XHeader, Group, Swiper, SwiperItem, Scroller, Datetime, XSwitch, XTextarea
}
}



